import React, { useContext, useState } from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUsersCog } from "@fortawesome/free-solid-svg-icons";
import Navigation from "../Navigation/Navigation";
import kacperPhoto from "../../assets/images/kacperPhoto.jpg";
import karolPhoto from "../../assets/images/karolPhoto.jpg";
import justynaPhoto from "../../assets/images/justynaPhoto.jpg";
import paulinaPhoto from "../../assets/images/paulinaPhoto.jpg";
import CoronaImg from "../../assets/images/coronaIcon.svg";
import { AppContext } from "../../store/store";

const HomePage = () => {
  const { restOfUsers, firstThree } = useContext(AppContext);
  const [placeNumber, setPlaceNumber] = useState(4);

const myImg = '../../assets/images/justynaPhoto.jpg';

  const restOfUsersList = restOfUsers.map((item) => (
    <div key={item.username} className="HomePage__anotherPlaces">
      <span className="HomePage__placeNumberSpan">{placeNumber}</span>
      <div className="HomePage__anotherPlacesBox">
        <div className="HomePage__anotherPlacesImageWrapper">
          <img
            src={require(`../../assets/images/photo.jpg`)}
            alt={item.profileImage}
            className="HomePage__anotherPlacesImage"
          />
        </div>
        <span className="HomePage__anotherPlacesNickname">{item.username}</span>
        <span className="HomePage__anotherPlacesPoints">{item.points}</span>
      </div>
    </div>
  ));

  return (
    <div className="HomePage">
      <Link to="/our-game-ranking/user" className="HomePage__userCogWrapper">
        <FontAwesomeIcon className="HomePage__userCog" icon={faUsersCog} />
      </Link>
      <h2 className="HomePage__header">Leaderboard</h2>
      <Navigation />

      <div className="HomePage__leadersWrapper">
        <div className="HomePage__thirdPlace">
          <span className="HomePage__placeNumber">3</span>
          <div className="HomePage__symbol">V</div>
          <div className="HomePage__image">
            <img className="HomePage__imageSrc" src={kacperPhoto} />
          </div>
          <span className="HomePage__nickname">
            {firstThree.length && firstThree[2].username}
          </span>
          <span className="HomePage__points">
            {firstThree.length && firstThree[2].points}
          </span>
        </div>

        <div className="HomePage__firstPlace">
          <span className="HomePage__placeNumber">1</span>
          <div className="HomePage__symbol">
            <img src={CoronaImg} className="HomePage__corona" alt="corona" />
          </div>
          <div className="HomePage__image HomePage__image--first">
            <img
              className="HomePage__imageSrc"
              src={karolPhoto}
              
            />
          </div>
          <span className="HomePage__nickname">
            {firstThree.length && firstThree[0].username}
          </span>
          <span className="HomePage__points">
            {firstThree.length && firstThree[0].points}
          </span>
        </div>

        <div className="HomePage__secondPlace">
          <span className="HomePage__placeNumber">2</span>
          <div className="HomePage__symbol">V</div>
          <div className="HomePage__image">
            <img className="HomePage__imageSrc" src={justynaPhoto} />
          </div>
          <span className="HomePage__nickname">
            {firstThree.length && firstThree[1].username}
          </span>
          <span className="HomePage__points">
            {firstThree.length && firstThree[1].points}
          </span>
        </div>
      </div>

      {restOfUsersList}
    

    </div>
  );
};

export default HomePage;
